import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import { BrowserRouter } from 'react-router-dom';
import { render } from 'react-dom';

ReactDOM.render( 
	
   <BrowserRouter> 
   		<React.StrictMode>
    		<App /> 
    	</React.StrictMode>
   </BrowserRouter>, 
   document.getElementById('root') 
) 







